import { AuthenticatedReq, OpenReq } from "Utils/Axios";

export default class SettingsApi {
  public static getGeneralSettings = async () => {
    return await AuthenticatedReq(`/business`, "GET");
  };

  public static putGeneralSettings = async (businessId: string, data: any) => {
    return await AuthenticatedReq(`/business/${businessId}`, "PUT", { data });
  };

  public static getCountryProvinces = async (country: string) => {
    return await OpenReq(`/countries/${country}.json`, "GET");
  };
}
